html,
body {
  margin: 0 auto;
  font-family: "Gothic A1";
  font-size: 20px;
}

body {
  background-color: #eeee;
}

.container {
  /* background-color: #eeeeee; */
  margin: 0 auto;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.informationText {
  max-width: 50%;
